@import '../../base/variables.scss';

.searchbar {
  height: 50px;
  border-radius: 11px;
  border: 0.2px solid #c5cace;
  background: #eef3f7;
  box-shadow: 0 0 25px 0 rgba(55, 84, 237, 0.05);
  color: rgba(8, 14, 41, 0.6);
  display: flex;
  align-items: center;
  flex-direction: row;
  min-width: 250px;

  input {
    width: 100%;
    font-size: 20px;
    height: 100%;
    font-weight: 400;
    outline: none;
    border: none;
    background: none;
    padding: 0 0px 0px 18px;
    &::placeholder {
      font-size: 20px;
      font-weight: 400;
      color: rgba(8, 14, 41, 0.5);
    }
  }

  .divider {
    height: 40px;
  }

  .searchButton {
    background: none;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 18px 0 16px;
    cursor: pointer;
  }
}

.info-bar-container {
    padding: 13px;
    background: $primary-gradient;
    border-radius: 24px;

    .info-container {
        padding-top: 10px;
        color: #fff;

        padding-left: 4px;

        .organ-title {
            display: -webkit-box;
            font-size: 32px;
            font-weight: 600;
            overflow: hidden;
            text-overflow: ellipsis;
            -webkit-line-clamp: 3;
            -webkit-box-orient: vertical;
        }

        .organ-category {
            display: -webkit-box;
            font-size: 20px;
            font-weight: 400;
            overflow: hidden;
            text-overflow: ellipsis;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
        }
    }
}

.point-menu {
  position: relative;
  width: 100%;
  // max-width: 550px;
  display: flex;
  height: 100%;
  user-select: all;
  // overflow-y: auto;

  .close-button-container {
    display: flex;
    height: 100%;

    .close-button {
      // position: absolute;
      margin-top: 14px;
      // left: -60px;
      height: 50px;
      margin-right: 10px;
      background: url('/src/assets/images/close.svg') center no-repeat;
      width: 40px;
      background-color: #131B62; 
      border-radius: 6px;
      border-style: none;
      cursor: pointer;
    }
    
    .close-button-open {
      transform: rotate(180deg);
    }
    
  }

  .menu-content {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    
    .hint-menu {
      position: fixed;
      right: 38%;
      top: 30vh;
      background: #F8F8F8; 
      border-radius: 12px; 
      border: 0.20px #C5CACE solid;
      padding: 15px;
      line-height: 26px;
      z-index: 15;
  
      .span-comb {
          color: #3754ED;
          font-size: 20px;
          font-weight: 700;
          word-wrap: break-word
      }
  
      .span-other {
          color: #3754ED;
          font-weight: 600;
          word-wrap: break-word
      }
    }
  
    .menu-container {
      width: 100%;
      flex: 1;
      position: relative;
      box-sizing: border-box;
      margin-top: 14px;
      padding: 20px;
      box-shadow: 0px 0px 25px rgba(55, 84, 237, 0.05); 
      border-radius: 24px; 
      overflow: hidden;
      overflow-y: auto;
      border: 0.20px #C5CACE solid;
      background: #EEF3F7;
    }
  }
}

.point-menu-close {
  height: fit-content;
}
